import { p as prisma } from "../../../../chunks/index4.js";
const load = async ({ locals }) => {
  const user = locals.user;
  const data = await prisma.user.findUnique({
    where: {
      id: user?.id
    },
    include: {
      area: {
        include: {
          candidates: true
        }
      }
    }
  });
  return {
    user: data,
    area: data.area,
    candidates: data.area.candidates
  };
};
export {
  load
};
